body {
    padding: 0;
    margin: 0;
}

#app {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
}

.grid {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto auto auto;
    column-gap: 10px;
    row-gap: 10px;
    background-color: lightgrey;
}

.square {
    width: 110px;
    height: 110px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
}

svg {
    stroke-width: 15px;
    stroke: #FF665C;
    fill: none;
    stroke-linecap: round;
}

.cross {
    stroke: #EC08BE;
}

.result {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 3em;
    font-weight: bold;
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
}

button {
    padding: 5px;
    font-size: 0.5em;
}

.footer {
    font-size: 0.8em;
    color: grey;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.footer a {
    text-decoration: none;
}

.inst {
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#smaller {
    font-size: 0.8em;
    color: darkgray;
}